@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body{min-width:360px;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box}p,h1,h2,h3,h4,h5{margin:0}.margin-top{margin-top:10px}.row{display:flex}.justify-cont-between{justify-content:space-between}.align-items-center{align-items:center}

.btn{font-family:"Roboto","Helvetica","Arial",sans-serif;font-size:14px;font-weight:600;min-width:130px;display:inline-block;color:#fff;background-color:green;text-align:center;border:1px solid transparent;padding:6px 16px;line-height:1.75;border-radius:10px;cursor:pointer;transition:0.3s linear}.btn:hover{transform:scale(1.1)}.btn-modal{margin:10px}

.Logo{height:100px;transition:0.3s linear}.Logo:hover{transform:scale(1.1)}

.headerStyled{display:flex;justify-content:space-between;padding-left:24px;padding-right:24px;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12);transition:box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;background-color:#fff;position:relative;align-items:center;min-height:130px;top:0;left:auto;position:fixed;width:100%;z-index:99;box-sizing:border-box}.headerStyled__Heading{font-family:"Fredoka One","Helvetica","Arial",sans-serif;font-size:50px;font-weight:400;text-transform:uppercase;text-align:center;color:red}@media (max-width: 600px){.headerStyled{flex-direction:column;margin:0;padding:10px}}

.wrapper{width:100%;height:100vh;position:fixed;top:0;left:0;z-index:100;background-color:rgba(0,0,0,0.3)}.modal{text-align:center;position:fixed;color:#fff;overflow:hidden;outline:0;left:calc(50% - 163px);top:300px;z-index:777}.modal-header{position:relative;font-size:28px;width:330px;display:flex;align-items:flex-start;justify-content:space-between;padding:25px;border-top-left-radius:10px;border-top-right-radius:10px}.modal-body{width:330px;position:relative;padding:10px 0}.modal-body .text-modal{padding-top:20px;margin:0}.modal-footer{width:330px;display:flex;align-items:center;justify-content:center;padding-bottom:20px;border-bottom-right-radius:10px;border-bottom-left-radius:10px}.close{position:absolute;top:0;right:0;z-index:2;padding:7px 20px 0 0;float:right;font-size:50px;font-weight:100;line-height:1;color:#fff;text-shadow:0 1px 0 #555454}.close:hover{color:red;text-decoration:none;cursor:pointer}

.sign-in{margin:auto;width:12vw;min-width:250px}

.group{position:relative;margin-top:25px;margin-bottom:15px}.group .form-input{background:none;background-color:#fff;color:grey;font-size:18px;padding:10px;display:block;width:100%;border:1px solid transparent;border-radius:10px;border-bottom:1px solid grey}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-24px;font-size:16px;color:#fff}.group input[type='password']{letter-spacing:0.5em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:15px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-24px;font-size:16px;color:#fff}

p{padding-top:15px}.Product{width:287px;height:100%;margin:16px;color:rgba(0,0,0,0.87);background-color:#fff;display:inline-block;padding:20px;position:relative;overflow:hidden;border-radius:10px;box-shadow:2px 3px 7px 0px rgba(0,0,0,0.24);transition:0.3s linear}.Product:hover{transform:scale(1.05)}.Product__NameAlbums{font-weight:500;font-size:17px}.Product__Author{font-weight:500;font-size:14px}.Product__Img{height:200px;object-fit:cover;width:90%;display:block;border-radius:10px;background-size:cover;background-repeat:no-repeat;background-position:center}.Product__Btn{width:23px;height:23px;padding:0;display:inline-block;background-color:#fff;border:none;outline:none}.Product__Type{font-size:14px;margin-right:7px}.Product__Price{font-size:21px;font-weight:700;color:blue}.Product__Ð¡urrency{font-size:16px}.Product__Discount{font-size:14px;color:grey;text-decoration:line-through}.Productt__id{font-size:12px}

.Favorite__Btn{width:23px;height:23px;padding:0;display:inline-block;background-color:#fff;border:none}

.ProductList{width:100%;display:flex;flex-wrap:wrap;align-items:center;align-content:center;margin-bottom:30px;box-sizing:border-box;padding-top:150px;margin-left:auto;margin-right:auto;justify-content:center}@media (max-width: 600px){.ProductList{padding-top:235px}}@media (max-width: 395px){.ProductList{padding-top:300px}}

